import type { ListenerCallback, PluginListenerHandle } from '@capacitor/core';
import { CapacitorException } from '@capacitor/core';

import type {
  BluetoothLowEnergyPlugin,
  ConnectOptions,
  CreateBondOptions,
  DisconnectOptions,
  DiscoverServiceOptions,
  GetConnectedDevicesResult,
  GetServicesOptions,
  GetServicesResult,
  IsBondedOptions,
  IsBondedResult,
  IsEnabledResult,
  PermissionStatus,
  ReadCharacteristicOptions,
  ReadCharacteristicResult,
  ReadDescriptorOptions,
  ReadDescriptorResult,
  ReadRssiOptions,
  ReadRssiResult,
  RequestConnectionPriorityOptions,
  RequestMtuOptions,
  StartCharacteristicNotificationsOptions,
  StartForegroundServiceOptions,
  StartScanOptions,
  StopCharacteristicNotificationsOptions,
  WriteCharacteristicOptions,
  WriteDescriptorOptions,
} from './definitions';
import { PromiseQueue } from './queue';

export class BluetoothLowEnergyClient implements BluetoothLowEnergyPlugin {
  private readonly plugin: BluetoothLowEnergyPlugin;
  private readonly queue = new PromiseQueue();

  constructor(plugin: BluetoothLowEnergyPlugin) {
    this.plugin = plugin;
  }

  public async connect(options: ConnectOptions): Promise<void> {
    const timeout = options.timeout ?? 10000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(this.plugin.connect(options), timeout),
    );
  }

  public async createBond(options: CreateBondOptions): Promise<void> {
    const timeout = options.timeout ?? 10000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(this.plugin.createBond(options), timeout),
    );
  }

  public async disconnect(options: DisconnectOptions): Promise<void> {
    const timeout = options.timeout ?? 5000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(this.plugin.disconnect(options), timeout),
    );
  }

  public async discoverServices(
    options: DiscoverServiceOptions,
  ): Promise<void> {
    const timeout = options.timeout ?? 20000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(this.plugin.discoverServices(options), timeout),
    );
  }

  public async getConnectedDevices(): Promise<GetConnectedDevicesResult> {
    return this.plugin.getConnectedDevices();
  }

  public async getServices(
    options: GetServicesOptions,
  ): Promise<GetServicesResult> {
    const timeout = options.timeout ?? 5000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(this.plugin.getServices(options), timeout),
    );
  }

  public async initialize(): Promise<void> {
    return this.plugin.initialize();
  }

  public async isBonded(options: IsBondedOptions): Promise<IsBondedResult> {
    const timeout = options.timeout ?? 5000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(this.plugin.isBonded(options), timeout),
    );
  }

  public async isEnabled(): Promise<IsEnabledResult> {
    return this.plugin.isEnabled();
  }

  public async openAppSettings(): Promise<void> {
    return this.plugin.openAppSettings();
  }

  public async openBluetoothSettings(): Promise<void> {
    return this.plugin.openBluetoothSettings();
  }

  public async openLocationSettings(): Promise<void> {
    return this.plugin.openLocationSettings();
  }

  public async readCharacteristic(
    options: ReadCharacteristicOptions,
  ): Promise<ReadCharacteristicResult> {
    const timeout = options.timeout ?? 5000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(this.plugin.readCharacteristic(options), timeout),
    );
  }

  public async readDescriptor(
    options: ReadDescriptorOptions,
  ): Promise<ReadDescriptorResult> {
    const timeout = options.timeout ?? 5000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(this.plugin.readDescriptor(options), timeout),
    );
  }

  public async readRssi(options: ReadRssiOptions): Promise<ReadRssiResult> {
    const timeout = options.timeout ?? 5000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(this.plugin.readRssi(options), timeout),
    );
  }

  public async requestConnectionPriority(
    options: RequestConnectionPriorityOptions,
  ): Promise<void> {
    const timeout = options.timeout ?? 5000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(this.plugin.requestConnectionPriority(options), timeout),
    );
  }

  public async requestMtu(options: RequestMtuOptions): Promise<void> {
    const timeout = options.timeout ?? 5000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(this.plugin.requestMtu(options), timeout),
    );
  }

  public async startCharacteristicNotifications(
    options: StartCharacteristicNotificationsOptions,
  ): Promise<void> {
    const timeout = options.timeout ?? 5000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(
        this.plugin.startCharacteristicNotifications(options),
        timeout,
      ),
    );
  }

  public async startScan(options: StartScanOptions): Promise<void> {
    return this.plugin.startScan(options);
  }

  public async startForegroundService(
    options: StartForegroundServiceOptions,
  ): Promise<void> {
    return this.plugin.startForegroundService(options);
  }

  public async stopCharacteristicNotifications(
    options: StopCharacteristicNotificationsOptions,
  ): Promise<void> {
    const timeout = options.timeout ?? 5000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(
        this.plugin.stopCharacteristicNotifications(options),
        timeout,
      ),
    );
  }

  public async stopForegroundService(): Promise<void> {
    return this.plugin.stopForegroundService();
  }

  public async stopScan(): Promise<void> {
    return this.plugin.stopScan();
  }

  public async writeCharacteristic(
    options: WriteCharacteristicOptions,
  ): Promise<void> {
    const timeout = options.timeout ?? 5000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(this.plugin.writeCharacteristic(options), timeout),
    );
  }

  public async writeDescriptor(options: WriteDescriptorOptions): Promise<void> {
    const timeout = options.timeout ?? 5000;
    return this.queue.enqueue(options.deviceId, () =>
      this.timeout(this.plugin.writeDescriptor(options), timeout),
    );
  }

  public async checkPermissions(): Promise<PermissionStatus> {
    return this.plugin.checkPermissions();
  }

  public async requestPermissions(): Promise<PermissionStatus> {
    return this.plugin.requestPermissions();
  }

  public async addListener(
    eventName: string,
    listenerFunc: ListenerCallback,
  ): Promise<PluginListenerHandle> {
    return this.plugin.addListener(eventName as any, listenerFunc);
  }

  public async removeAllListeners(): Promise<void> {
    return this.plugin.removeAllListeners();
  }

  private async timeout<T>(promise: Promise<T>, time: number): Promise<T> {
    return Promise.race([
      promise,
      new Promise<never>((_, reject) =>
        setTimeout(() => {
          reject(this.createTimeoutException());
        }, time),
      ),
    ]);
  }

  private createTimeoutException(): CapacitorException {
    return new CapacitorException('Operation timed out');
  }
}
